import { AfterViewInit, Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { JobOpening } from 'src/app/shared/models/JobOpening';
import { ScriptsService } from 'src/app/shared/services/client/scripts.service';
import { JobService } from 'src/app/shared/services/recruitment/job.service';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit, AfterViewInit {
  jobList:any[]=[];
  opening$: Observable<JobOpening[]> = this._js.getJobOpeningsApiSort({_sort: 'created_at', _order: 'desc'});

  constructor(private _titel: Title, private router: Router, private _js: JobService, private ss:ScriptsService) {

  }
  ngAfterViewInit(): void {
    setTimeout(() => {
      this.scrollToTop();
    }, 900);
    // throw new Error('Method not implemented.');
  }

  ngOnInit(): void {
        this.ss.meta.addTags([
      { name: 'description', content: 'Take your skills to the next level, explore “Online” jobs from around the world number one #1 and most popular HR recruitment website. Take your next career step. Search for jobs, read career advice and sign up for alerts on the latest vacancies from top companies.' },
      { name: 'og:title', content: 'Find The Right Job with Zinder' },
      { name: 'twitter:title', content: 'Find The Right Job with Zinder' },
      { name: 'og:description', content: 'Search for jobs &amp; read career advice' },
      { name: 'twitter:description', content: 'Search for jobs &amp; read career advice' },
    ],true);
    this._titel.setTitle('Find the Right Job Vacancies in Nigeria | with Zinder (Zinder.com.ng)')
    this.scrollToTop();
    this.opening$.subscribe(c=>{

      setTimeout(() => {
        this.scrollToTop();
      }, 900);
    })
  }

  scrollToTop(){
    window.scrollTo({
      top: 400,
      behavior: 'smooth',
    });
  }
  showDetails(code:any){
    this.router.navigate(['jobs/'+code]);
  }

}
