import { Component, OnInit } from '@angular/core';
import { Observable, from } from 'rxjs';
import { tap, shareReplay, map } from 'rxjs/operators';
import { UserService } from '../shared/services/user/user.service';
import { Router } from '@angular/router';
import { AuthService } from '../shared/services/Index';
import { DeviceService } from '../shared/services/client/device.service';
import { AppService } from '../shared/services/client/app.service';
import { BookingService } from '../shared/services/booking/booking.service';
import { ScriptsService } from '../shared/services/client/scripts.service';
import { PassageUser } from '@passageidentity/passage-elements/passage-user';
import { SocketService } from '../shared/services/client/socket.service';

@Component({
  selector: 'app-client',
  templateUrl: './client.component.html',
  styleUrls: ['./client.component.scss']
})
export class ClientComponent implements OnInit {
  content:string = '🤯 Announcing Our Enterprise Solution For Everyone! Learn How to Get One → SUBSCRIBE NOW! SEPTEMBER SALES, 25% OFF LICENSE FOR ZINDER SOFTWARE ENTERPRISE. ONLY VALID TILL 30TH OF SEPTEMBER, 2023. ';
  today:any = new Date().toISOString().slice(0, 16);
  $user: Observable<any> = this._us.user$;
  $apps: Observable<any> = this._apps.apps$;
  urL: string = '/';
  _routeEvent$: Observable<any> = this.router.events.pipe(
    shareReplay(1),
  );
  isloggedIn: any = null;
  meeting:any = {
    name: '',
    email:'',
    phone: '',
    app: '',
    free_day_1: '',
    free_day_2: '',
    created_at: '',
    updated_at: '',
  }
  constructor(private ss: ScriptsService,private socketService: SocketService
    ,private _apps:AppService, private bookingService: BookingService,
    private _us: UserService, private router: Router, public _as: AuthService, private _ds: DeviceService) {
  }
  gotoAbout(stt:any){
    this._ds.showSpinner();
    this.router.navigate(['']).finally(()=>{
      setTimeout(() => {
        this.ss.scrollToId(stt);
        this._ds.hideSpinner();
      }, 600);
    })
  }
  async gotoNav(stt:any){
    this._ds.showSpinner();
    this.router.navigate([`/${stt}`]).finally(async ()=>{
      try {
        const element = await this.ss.$scrollToId(stt);
      } catch (error) {

      }
      setTimeout(() => this._ds.hideSpinner(), 300);
    })
  }
  ngOnInit(): void {
    this.isloggedIn = this._as.sessionAuth;
    console.log(this.isloggedIn);
    this.$user.subscribe(c=>console.log(c))
    this.urL = `/${window.location.href.split("/")[3]}`;
    this._routeEvent$.subscribe((e: any) => {
      if (e && e.urlAfterRedirects) {
        this.urL = e.urlAfterRedirects;

        this.ss.currentUrl.next(this.urL);
      }
    });
    this.socketService.socket.on('connect',()=>this.socketService.connected())

  }

  submit(){
    let message:string = ``;
    this.meeting.created_at = this.today;
    this.meeting.updated_at = this.today;
    let toastOptions = this._ds.toastOptions;
    let timeOut:number = 0;
    for (const [key, value] of Object.entries(this.meeting)) {
      const v: any = value;
      if(!v){
        message+=`${key.replace('_', ' ').toLocaleLowerCase()} required\n`;
        timeOut+=2;
      }
      if(key =='email' && !(<string>v).includes('@')){
        message+=`Please enter a valid ${key.replace('_', ' ').toLocaleLowerCase()} address\n`;
        timeOut+=2;
      }
    }
    if(message.length > 0){
      timeOut = timeOut*1000;
      toastOptions = {...toastOptions, timeOut};

      this._ds.oErrorNotification(`Unable to schedule meeting`, `Invalid form was submitted.\nReason: ${message}`, toastOptions);
      return;
    }
    this.bookingService.add(this.meeting).subscribe(
      booking=>{
        this._ds.openSuccessNotification('Schedule submitted', 'Your schedule has been submitted');
        this.meeting = {
          name: '',
          email:'',
          phone: '',
          app: '',
          free_day_1: '',
          free_day_2: '',
          created_at: '',
          updated_at: '',
        }
      }
    )
  }
}
