import { AfterViewChecked, Component, OnInit } from '@angular/core';
import { Plan } from 'src/app/shared/models/Plan';
import {Observable} from 'rxjs';
import { PlanService } from 'src/app/shared/services/plan/plan.service';
import { Router } from '@angular/router';
import { App } from 'src/app/shared/models/App';
import { AppService } from 'src/app/shared/services/client/app.service';
@Component({
  selector: 'client-pricing',
  templateUrl: './pricing.component.html',
  styleUrls: ['./pricing.component.scss']
})
export class PricingComponent implements OnInit, AfterViewChecked {
  apps: Observable<App[]> = this._app.apps$;

  prices$:Observable<Plan[]> = this._ps.getPlansApi$;
  constructor(private _ps: PlanService, private _app: AppService, private router:Router) { }
  ngAfterViewChecked(): void {

  }

  ngOnInit(): void {
    setTimeout(
      ()=>{
        window.scrollTo({
          top: 100
      })
      }, 900
    )
  }
  navigateTo(route:string){
    this.router.navigate([route]).finally(
      ()=>this.scrollToTop()
    );
  }
  scrollToTop(){
    window.scrollTo({
      top: 100,
      behavior: 'smooth',
    });
  }

}
